#bands {

  $dark-grey: #000000;
  font-family: NeutraText-Book, sans;

  color: white;

  nav#scrollnav {
          display: block;
          display: none;
          position: fixed;
          right: 30px;
          top: 50%;
          -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
          transform: translateY(-50%);
          text-align: right;
          z-index: 100;
          a {
                  display: block;
                  line-height: 25px;
                  position: relative;
                  padding-right: 20px;
                  color: #ffffff;
                  text-decoration: none;
                  &:hover {
                          .nav-label {
                                  -webkit-transform: translateX(0);
                                  -ms-transform: translateX(0);
                                  transform: translateX(0);
                                  opacity: 1;
                                  visibility: visible;
                          }
                  }
          }
          .nav-dot {
                  display: block;
                  width: 10px;
                  height: 10px;
                  position: absolute;
                  right: 0;
                  top: 50%;
                  background-color: rgba(255, 255, 255, 0.9);
                  border-radius: 100%;
                  -webkit-transform: translateY(-50%);
                  -ms-transform: translateY(-50%);
                  transform: translateY(-50%);
                  -webkit-transition: all 0.3s linear;
                  transition: all 0.3s linear;
          }
          .nav-label {
                  display: block;
                  opacity: 0.5;
                  visibility: hidden;
                  -webkit-transform: translateX(-10px);
                  -ms-transform: translateX(-10px);
                  transform: translateX(-10px);
                  -webkit-transition: all 0.2s cubic-bezier(.17, .67, .83, .67);
                  transition: all 0.2s cubic-bezier(.17, .67, .83, .67);
          }
          ul {
                  margin: 0;
                  padding: 0;
          }
          li {
                  width: auto;
                  list-style: none;
          }
  }

  /////////////////////////////////////////////////////////////////////////////////
  .header-container {
          text-align: center;
          color: #ffffff;
          .wrapper {
                  padding-top: 120px;
                  z-index: 2;
                  position: relative;
          }
          .bcg {
                  background-image: url(../img/blue_stars.jpg);
                  background-size: cover;
          }
  }
  .bcg {
          background: no-repeat center center;
          background-size: cover;
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 1;
          opacity: 0.5;
  }
  #slide01 {
          .bcg {
                  background: url(../img/anode_dl.png) no-repeat center center;
                  background-size: cover;
          }
  }
  #slide02 {
          .bcg {
                  background: url(../img/anode_yellow.png) no-repeat center center;
                  background-size: cover;
          }
  }
  #slide03 {
          .bcg {
                  background: url(../img/img_mountains04.jpg) no-repeat center center;
                  background-size: cover;
                  opacity: 0.7;
          }
  }
  nav#scrollnav.is-dark {
          a {
                  color: $dark-grey;
          }
          .nav-dot {
                  background-color: rgba(0, 0, 0, 0.5);
          }
  }
  nav#scrollnav.is-light {
          a {
                  color: #ffffff;
          }
          .nav-dot {
                  background-color: rgba(255, 255, 255, 0.5);
          }
  }
  .scroll-hint {
          color: $dark-grey;
          position: absolute;
          bottom: 30px;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
          padding: 10px 40px;
          border: 3px #ffffff solid;
          font-size: 13px;
          font-weight: bold;
          font-family: 'Roboto Condensed', Helvetica, Helvetica Neue, Arial, sans-serif;
          background-color: rgba(255, 255, 255, 0.9);
          z-index: 2;
  }
  p {
          font-size: 16px;
  }
  .fs {
          height: 100vh;
  }
  .slide {
          color: #ffffff;
          position: relative;
          .wrapper {
                  padding-top: 30px;
                  text-align: center;
                  position: relative;
                  z-index: 2;
          }
  }
  .slide#slide01 {
          .wrapper {
                  position: absolute;
                  top: 10%;
          }
  }
  .slide#slide02 {
          .wrapper {
                  position: absolute;
                  bottom: 10%;
          }
  }
  .slide#slide03 {
          .button {
                  border-color: #ffffff;
                  color: $dark-grey;
                  background-color: rgba(255, 255, 255, 0.9);
                  &:hover {
                          border-color: $dark-grey;
                          background-color: $dark-grey;
                          color: #ffffff;
                  }
          }
          .wrapper {
                  padding-top: 100px;
          }
  }
  .slide#slide04 {
          background-color: #be4c2c;
  }
  .button {
          text-decoration: none;
          color: #ffffff;
          border: 1px rgba(255, 255, 255, 0.7) solid;
          padding: 8px 15px;
          margin-top: 15px;
          display: inline-block;
          position: relative;
          &:hover {
                  border-color: #ffffff;
                  background-color: #ffffff;
                  color: #000000;
          }
  }
  .content-block {
          background-color: $dark-grey;
          color: rgba(255, 255, 255, 0.9);
          .wrapper {
                  padding: 30px 0;
                  text-align: center;
          }
  }
  .no-touch {
          .content-block {
                  .wrapper {
                          visibility: hidden;
                          opacity: 0;
                  }
          }
          .content-block.is-active {
                  .wrapper {
                          visibility: visible;
                          opacity: 1;
                          -webkit-transform: translate3d(0, 0, 0);
                          transform: translate3d(0, 0, 0);
                  }
          }
          .slideInUp {
                  visibility: hidden;
                  opacity: 0;
                  -webkit-transition: all 0.7s ease-out;
                  transition: all 0.7s ease-out;
                  -webkit-transform: translate3d(0, 50px, 0);
                  transform: translate3d(0, 50px, 0);
          }
          .is-active {
                  .slideInUp {
                          visibility: visible;
                          opacity: 1;
                          -webkit-transform: translate3d(0, 0, 0);
                          transform: translate3d(0, 0, 0);
                  }
          }
  }
  .footer-container {
          text-align: center;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.5);
          background-color: #06060d;
          h3 {
                  margin: 0;
                  padding: 20px 0;
                  font-weight: normal;
          }
          a {
                  color: rgba(255, 255, 255, 0.5);
          }
  }
  .slideInUp.slideInUp2 {
          -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
  }
  .slideInUp.slideInUp3 {
          -webkit-transition-delay: 0.4s;
          transition-delay: 0.4s;
  }
  #iht_logo {
          width: 100px;
          margin-bottom: 30px;
  }
  .hidden {
          display: none !important;
          visibility: hidden;
  }
  .visuallyhidden {
          border: 0;
          clip: rect(0 0 0 0);
          height: 1px;
          margin: -1px;
          overflow: hidden;
          padding: 0;
          position: absolute;
          width: 1px;
  }
  .visuallyhidden.focusable {
          &:active {
                  clip: auto;
                  height: auto;
                  margin: 0;
                  overflow: visible;
                  position: static;
                  width: auto;
          }
          &:focus {
                  clip: auto;
                  height: auto;
                  margin: 0;
                  overflow: visible;
                  position: static;
                  width: auto;
          }
  }
  .invisible {
          visibility: hidden;
  }
  .clearfix {
          &:before {
                  content: " ";
                  display: table;
          }
          &:after {
                  content: " ";
                  display: table;
                  clear: both;
          }
          *zoom: 1;
  }

  @media only screen and (min-width: 768px) {
          body {
                  font-weight: 100;
          }
          h1 {
                  font-size: 3em;
          }
          p {
                  font-size: 20px;
          }
          .header-container {
                  overflow: hidden;
                  position: relative;
                  .wrapper {
                          position: absolute;
                          top: 50%;
                          left: 50%;
                          -webkit-transform: translate(-50%, -64%);
                          -ms-transform: translate(-50%, -64%);
                          transform: translate(-50%, -64%);
                          margin: 0;
                          padding: 0;
                  }
                  &:before {
                          font-size: 70em;
                  }
          }
          .scroll-hint {
                  font-size: 20px;
          }
          .bcg {
                  opacity: 1;
          }
          .slide {
                  overflow: hidden;
                  overflow-x: hidden;
                  section {
                          max-width: 400px;
                  }
                  .wrapper {
                          padding-top: 120px;
                          position: absolute;
                          top: 50%;
                          left: 50%;
                          -webkit-transform: translate(-50%, -50%);
                          -ms-transform: translate(-50%, -50%);
                          transform: translate(-50%, -50%);
                          text-align: left;
                          width: 100%;
                          margin: 0;
                  }
          }
          .slide#slide01 {
                  color: $dark-grey;
                  color: #ffffff;
                  .wrapper {
                          top: 20%;
                          text-align: center;
                  }
                  section {
                          margin: 0 auto;
                  }
          }
          .slide#slide02 {
                  color: $dark-grey;
                  color: #ffffff;
                  .wrapper {
                          top: 20%;
                          text-align: center;
                          top: auto;
                          bottom: 40%;
                          left: 10%;
                          text-align: left;
                          -webkit-transform: translate(0);
                          -ms-transform: translate(0);
                          transform: translate(0);
                          padding: 0;
                  }
                  section {
                          margin: 0 auto;
                          margin: 0;
                  }
          }
          .slide#slide03 {
                  color: $dark-grey;
                  .wrapper {
                          top: 20%;
                          text-align: center;
                          padding: 0;
                          top: 15%;
                          -webkit-transform: translate(-50%, 0);
                          -ms-transform: translate(-50%, 0);
                          transform: translate(-50%, 0);
                  }
                  section {
                          margin: 0 auto;
                          margin: 0 auto;
                          max-width: 570px;
                  }
                  .button {
                          border-color: rgba(0, 0, 0, 0.7);
                          background-color: transparent;
                          color: $dark-grey;
                  }
                  .bcg {
                          opacity: 1;
                  }
          }
          nav#scrollnav {
                  display: block;
          }
          .content-block {
                  .wrapper {
                          padding: 120px 0;
                  }
          }
  }
}
